@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700&display=swap);
.Badge{border-radius:0.8em;color:#fff;height:2.1em;line-height:2.4em;margin-right:0.8em;text-align:center;width:2.1em}

.Section{padding:2em}.Section h1{color:#46494e}.Section .Caption{color:#9da2a7;font-size:0.9em;margin-top:0.3em}.Section main{margin-top:2em}

.Avatar{display:flex;margin-bottom:2.5em}.Avatar>img{border-radius:50%;max-width:3em}.Avatar .Info{margin:0 1.4em 0 0.8em}.Avatar .Info .Name{color:#46494e;display:block;font-weight:500;margin:0.2em 0 0;white-space:nowrap}.Avatar .Info .Mail{color:#adacb1;font-size:0.8em}.Avatar .Settings{background-color:#dfe0f1;border:0;border-radius:50%;color:#9e9eb6;cursor:pointer;height:2.7em;line-height:3.05em;margin-top:0.15em;text-align:center;width:2.7em}.Avatar .Settings:hover{background-color:#d2d3e7}

.Sidebar{background-color:#f4f3f9;display:flex;flex-direction:column;padding:2em;width:18em}.Sidebar h4{color:#a6a5ab;font-size:1em;font-weight:500}.Sidebar .SideMenu{margin-bottom:2em}.Sidebar .SideMenu ul>li{display:flex;margin:1.8em 0;position:relative}.Sidebar .SideMenu ul>li>a{color:#68676d;display:flex;font-size:1em;font-weight:500;line-height:2em;text-decoration:none;width:100%;z-index:1}.Sidebar .SideMenu ul>li>a:hover{color:#333}.Sidebar .SideMenu ul>li:before{background:rgba(143,132,174,0.1);border-radius:1em;content:'';display:none;height:calc(100% + 1.6em);left:-1em;position:absolute;top:-0.8em;width:calc(100% + 2em);z-index:0}.Sidebar .SideMenu ul>li:hover:before{display:block}

.Layout{display:flex;height:100%}

.UserPage h3{font-weight:400;margin-bottom:25px}.UserPage h3 span{font-size:16px;font-weight:300;margin-left:10px}.UserPage table td{padding:7px 0}.UserPage table td:first-child{font-weight:300;padding-right:30px}

.UsersList>li{display:block;margin-bottom:10px}

*{margin:0;padding:0}html,body,#root{height:100%}body{font-family:'Roboto', 'Arial', sans-serif}

